.prosemirrorWrapper {
  flex: 1 1 auto;
  overflow-y: scroll;
  padding: 1rem; }
  .theme-dark .prosemirrorWrapper {
    background-color: #1c2731; }

.ProseMirror {
  overflow-y: scroll;
  outline: none;
  font-family: "PT Mono", consolas, courier, monospace;
  font-size: 1.4rem; }
  .theme-dark .ProseMirror {
    color: #dbdbdb; }
  .ProseMirror p {
    margin: 0; }
